import React from "react";
import cx from "classnames";
import styles from "./Header.module.scss";

type HeaderTheme = {
  backgroundColor: string;
  color: string;
  accentColor: string;
};

type HeaderProps = {
  theme: HeaderTheme;
};

const Header = ({ theme }: HeaderProps) => {
  const { backgroundColor, color, accentColor } = theme;

  const classes = cx(styles.header, {});

  const headerStyle = {
    "--accent-color-primary": accentColor || "#0078f2",
    backgroundColor,
    color,
  };

  return <header className={classes} style={headerStyle}></header>;
};

export default Header;
